nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    margin-top: 2rem;
}

.searchBar {
    display: flex;
    border: 1px solid rgba(190, 190, 190, 0.5);
    border-radius: 25px;
    height: 4rem;
    width: 60%;
    justify-content: space-around;
    align-items: center;
    margin-top: 5px;
    box-shadow: 5px 5px 20px rgba(181, 181, 181, 0.2);
}

.searchBar input{
    border: none;
    outline: none;  
    height: 100%;  
    width: 90%;
    border-radius: 25px;
}

.searchBar h3 {
    width: 10%;
}

.navBarIcons {
    display: flex;
    align-items: center;
    width: 20%;
}

.iconContainer {
    max-width: 100%;
}

.menu, .menuIcons {
    display: none;
}


@media screen and (max-width: 1275px) {
    .menu {
        display: block;
        cursor: pointer;
    }
    .iconContainer {
        display: none;
    }

    .navBarIcons {
        width: 10%;
    }

    .menuIcons {
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .menuIcons div {
        max-width: 100%;
    }

    .hidden {
        display: none;
    }
}

@media screen and (max-width: 1025px) {
    nav h3 {
        font-size: 1.5rem;
    }
}

